
import axios from 'axios';

export interface BaseTransport {
    connect: (url: string) => BaseTransport;
    emit: (event: string, args: any) => void;
}

export const DebugTransport: BaseTransport = {

    connect: function (url: string) {
        console.log(`connect to ${url} endpoint`);
        return this;
    },

    emit: function (event: string, args: any) {
        console.log(event, JSON.stringify(args));
    }
}

export const AxiosTransport: BaseTransport = {

    connect: function (baseUrl: string) {
        if (baseUrl || baseUrl.length === 0) {
            throw new Error("Missing `url` parameter");
        }
        this.baseUrl = baseUrl;
        return this;
    },
    emit: function (endpoint: string, args: any) {
        axios
            .post(`${this.baseUrl}/${endpoint}`, args)
            .then(function (response: any) {
                console.log(response);
            })
            .catch(function (error: any) {
                console.log(error)
            });
    }
}